(function (doc, win) {
    var docEl = doc.documentElement,
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
        recalc = function () {
            //jsè·åå½åçè®¾å¤å¤§å°ï¼document.documentElement.clientWidth;
            var clientWidth = docEl.clientWidth;//è·åè®¾å¤çå®½åº¦
            if (!clientWidth) return;
            if (clientWidth >= 750) {//å®½åº¦>750 å¹³æ¿ æèæ¡é¢ 
                docEl.style.fontSize = '100px';//æ ¹åç´  
            } else {//ç§»å¨ç«¯çéé 
                //è®¾ç½®ï¼htmlæ ¹åç´ å¤§å°   100è®¾è®¡ç¨¿çhtmlå¤§å°  750è®¾è®¡ç¨¿ï¼æ ¹æ®è®¾è®¡ç¨¿ååï¼
                //æµè¯è®¾è®¡ç¨¿å¤§å°ï¼æµéçå¼px  pxè½¬rem  æµéçpx/100=?rem  ç´æ¥csséé¢å?rem
                docEl.style.fontSize = 100 * (clientWidth / 750) + 'px';
            }
        };
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);
    doc.addEventListener('DOMContentLoaded', recalc, false);
})(document, window);



