                </div>
                <div class="bold">{{ covData.currentConfirmedCount |division}}</div>
                <strong>ç°å­ç¡®è¯</strong>
              </li>
              <li>
                <div class="number">
                  <span>è¾æ¨æ¥</span>{{ covData.suspectedIncr | number }}
                </div>
                <div class="bold">{{ covData.suspectedCount |division }}</div>
                <strong>å¢å¤è¾å¥</strong>
              </li>
              <li>
                <div class="number">
                  <span>è¾æ¨æ¥</span>{{ covData.seriousIncr | number }}
                </div>
                <div class="bold">{{ covData.seriousCount |division }}</div>
                <strong>ç°å­æ çç¶</strong>
              </li>
              <li>
                <div class="number">
                  <span>è¾æ¨æ¥</span>{{ covData.confirmedIncr | number }}
                </div>
                <div class="bold">{{ covData.confirmedCount | division}}</div>
                <strong>ç´¯è®¡ç¡®è¯</strong>
              </li>
              <li>
                <div class="number">
                  <span>è¾æ¨æ¥</span> {{ covData.deadIncr | number }}
                </div>
                <div class="bold">{{ covData.deadCount | division}}</div>
                <strong>ç´¯è®¡æ­»äº¡</strong>
              </li>
              <li>
                <div class="number">
                  <span>è¾æ¨æ¥</span> {{ covData.curedIncr | number }}
                </div>
                <div class="bold">{{ covData.curedCount | division }}</div>
