<script>
export default {
    name:'CovidNum',
    data(){
        return {
            // covData:{},

        }
    },
    props: ["covData"],
    //å®ä¹è¿æ»¤å¨
    filters: {
        //æ°å­>0 è¡¥+
        number: function (val) {
        val = val > 0 ? "+" + val : val;
        if (val == 0) {
            return "";
        }
        return val;
        },
        //å¤çæ¶é´æ ¼å¼--- js:å¹´ææ¥
        time: function (time) {
        time = new Date().toLocaleString("chinese", { hour12: false });
        // console.log(time)
        return time;
        //æå¨è·åï¼
        // var y = data.getFullYear();
        // var m = data.getMonth()+1;
        // var d = data.getDate();
        // return y+'-'+m+'-'+d;
        },
        //js ä¸­æ°å­å éå·å¤ç(æ¯ä¸ä½å éå·)
        division: function (num) {
        if (num == undefined) return "";
        // console.log('num---',num);
        //æ¹å¼1ï¼
        // console.log('æ ¼å¼---',num.toLocaleString());
        return num.toLocaleString();
        //æ¹å¼2ï¼æ­£åå¤ç
        //   return num.toString().replace(/\d+/, function (n) {
        //     return n.replace(/(\d)(?=(?:\d{3})+$)/g, "$1,");
        //   });
        },
    },

}
</script>

<style scoped>
.covid-num{
    margin-top: 0.3rem; 
    padding: 0rem;   
    background-color:white;
}
.content {
    background: #fff;
    padding: 0.3rem;
    min-height: 2rem;
    border-radius: 0.2rem 0.2rem 0 0;
}
.title {
      color: #666;
      margin-bottom: 0.2rem;
}

.wrap {
    display: flex;
    flex-wrap: wrap;
    padding-top: 0.2rem;
    border-radius: 0.1rem;
    box-shadow: 0 0 3px 2px rgba(194, 192, 192, 0.2);
}
li {
    width: 33.33333%;
    text-align: center;
    margin-bottom: 0.24rem;
    position: relative;
    font-size: 0.24rem;
} 
.number span{
    color: #777;
    font-weight: bold;
} 
.number strong {
    color: #333;
}   

li::after {
    content: "";
    display: block;
    position: absolute;
    right: 1px;
    top: 0.2rem;
    height: 0.8rem;
    border-right: 1px solid #eee;
}
li:nth-child(3n)::after {
    content: "";
    border-right: 0;
}
.bold {
    font-weight: bold;
    font-size: 0.36rem;
}
li:nth-child(1) {
    color: red;
}
li:nth-child(2) {
    color: rgb(18, 143, 14);
}
li:nth-child(3) {
    color: orange;
}
li:nth-child(4) {
    color: rgb(189, 19, 98);
}
li:nth-child(5) {
    color: rgb(223, 208, 7);
}
li:nth-child(6) {
    color: rgb(8, 110, 194);
}


</style>


