<template>
    <div>
        <swiper ref="mySwiper" :options="swiperOptions">
        <swiper-slide v-for="item in banner" :key='item.title'>
            <img :src="item.image" alt="" width="100%" />
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <!-- è½®æ­çå¾çæé® -->
        <ul class="list">
            <li
                v-for="(item,index) in banner" :key='item.title'
                :class="{ active: index == num }"
                @click="changeSwiper(index)"
            >
                {{ item.title }}
            </li>
        </ul>
    </div>

</template>

<script>
import api from '../../api/index'
export default {
name: 'carrousel',
data() {
    // æ¥åthis 
    let that = this;
    return {
        num: 0,
        arr: [1, 2, 3, 4, 5],
        banner:[],//è½®æ­å¾æ°æ®
        //swiperéç½®åå®¹
        swiperOptions: {
            pagination: {
            el: ".swiper-pagination",
            },
            autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
            },
            loop: true,
            on: {
                //åè°å½æ°ï¼swiperä»ä¸ä¸ªslideè¿æ¸¡å°å¦ä¸ä¸ªslideå¼å§æ¶æ§è¡
                slideChangeTransitionStart: function () {
                    //   console.log('è½®æ­åæ¢äº---',this.activeIndex);//1 - 2 3 4 5 6 
                    //ä¿®æ¹num=[0,1,2,3,4]
                    //è½®æ­åæ¢çæ¶åè·åä¸æ activeIndex  ç¶åä¿®æ¹numåæ­¥åæ¢é«äº®
                    //å¤æ­ï¼
                    if(this.activeIndex == that.arr.length+1){
                            that.num =0;
                    }else{
                        that.num = this.activeIndex-1;
                    }
                },
            },
        },
    }
},
computed: {
    swiper() {
    return this.$refs.mySwiper.$swiper
    }
},
mounted() {
    api.getSwiperBanner()
    .then(res=>{
        console.log('--è½®æ­å¾--',res.data);
        this.banner = res.data.result;
    })
},
methods: {
    changeSwiper(index) {
      //é«äº®éä¸­
      this.num = index;
      //è®©swiperåæ¢
      this.swiper.slideTo(index + 1, 1000, false);
    },
  },
}
</script>

<style lang='less' scoped>
.list {
  display: flex;
  li {
    flex: 1;
    padding: 0.1rem;
    font-size: 0.24rem;
    line-height: 0.32rem;
    margin: 0.1rem;
    background: #eee;
    color: #666;
    text-align: center;
    display: flex;
    align-items: center;
  }
  .active {
    background: rgb(80, 116, 173);
    color: #fff;
  }
}
</style>


